@import "../node_modules/normalize.css/normalize.css";
@import 'styles/function.scss';

*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Montserrat';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, body {
  overflow: hidden;
}

ol, ul {
	list-style: none;
}

svg {
  outline: none;
}
