@import 'styles/function.scss';

.demo {
  height: 100vh;
  overflow: auto;

  padding: rem(10);
}

.demoSpreadSheet {
  width: 100%;
  height: rem(500);
}

.demoBtn {
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: rem(4);
  background-color: rgb(128, 175, 34);

  min-width: rem(200);

  padding: rem(10);
  margin-top: rem(10);
  margin-right: rem(10);

  &:last-child {
    margin-right: 0;
  };

  &:active {
    box-shadow: 0 0 5px 5px #ccc;
  }

  &Red {
    background-color: coral;
  }

  &Brown {
    background-color: moccasin;
  }
}
